import { Controller } from 'stimulus';

const toDigits = number => (number > 9 ? number : `0${number}`);

const toTime = date => `${toDigits(date.getHours())}:${toDigits(date.getMinutes())}`;

// const toDate = date => `${date.getFullYear()}-${toDigits(date.getMonth() + 1)}-${toDigits(date.getDate())}`;

// const toDateTime = date => `${toDate(date)} ${toTime(date)}`;

const toJapaneseDate = date => `${date.getFullYear()}${toDigits(date.getMonth() + 1)}${toDigits(date.getDate())}`;

const diffInDays = (first, second) => Math.floor((second - first) / (1000 * 60 * 60 * 24));

class CreateEventController extends Controller {
    static targets = ['start', 'end', 'title'];

    connect() {
        this.allDayEvent = false;
    }

    open(event) {
        const details = event.detail;
        if (details.fromView === 'dayGridMonth') {
            if (diffInDays(details.start, details.end) === 1) {
                this.createSingleDayEvent(details);
            } else {
                this.createMultipleDaysEvent(details);
            }
        }
        if (details.fromView === 'timeGridWeek') {
            if (diffInDays(details.start, details.end) === 0) {
                this.createSingleDayEvent(details);
            } else {
                this.createMultipleDaysEvent(details);
            }
        }

        if (details.fromView === 'timeGridDay') {
            this.createSingleDayEvent(details);
        }

        window.$(this.element).modal('show');
    }

    createSingleDayEvent(data) {
        const now = new Date();
        let minTime;
        let maxTime;
        if (data.fromView === 'dayGridMonth') {
            minTime = new Date();
            maxTime = new Date();
            minTime.setHours(now.getMinutes() > 30 ? now.getHours() + 1 : now.getHours(), now.getMinutes() > 30 ? 0 : 30, 0);
            maxTime.setHours(now.getHours() + 1, now.getMinutes() > 30 ? 30 : 0, 0);
        } else {
            minTime = data.start;
            maxTime = data.end;
        }

        this.startTarget.innerHTML = `<input type="text" class="form-control" id="startTime" name="start" data-controller="datepicker" value="${toJapaneseDate(
            minTime
        )} ${toTime(minTime)}">`;
        this.endTarget.innerHTML = `<input type="text" class="form-control" id="endTime" name="end" data-controller="datepicker" value="${toJapaneseDate(
            maxTime
        )} ${toTime(maxTime)}">`;
        this.titleTarget.innerHTML = `${toJapaneseDate(data.start)}`;
    }

    createMultipleDaysEvent(data) {
        const now = new Date();

        const startTime = data.start;
        const endTime = data.end;

        if (data.fromView === 'dayGridMonth') {
            endTime.setHours(now.getHours() - 24, now.getMinutes());
            startTime.setHours(now.getHours(), now.getMinutes());
        }

        this.startTarget.innerHTML = `<input type="text" class="form-control" id="startTime" name="start" data-controller="datepicker" value="${toJapaneseDate(
            startTime
        )} ${toTime(startTime)}">`;
        this.endTarget.innerHTML = `<input type="text" class="form-control" id="endTime" name="end" data-controller="datepicker" value="${toJapaneseDate(
            endTime
        )} ${toTime(endTime)}">`;
        this.titleTarget.innerHTML = '';
    }

    toggleAllDayEvent() {
        this.allDayEvent = !this.allDayEvent;
        if (this.allDayEvent) {
            const [startDate, startTime] = this.startTarget.querySelector('#startTime').value.split(' ');
            this.startTarget.innerHTML = `<input type="text" class="form-control" id="startTime" name="start" data-datepicker-date-only data-controller="datepicker" value="${startDate}">`;
            this.oldStartTime = startTime;
            const [endDate, endTime] = this.endTarget.querySelector('#endTime').value.split(' ');
            this.endTarget.innerHTML = `<input type="text" class="form-control" id="endTime" name="end" data-datepicker-date-only data-controller="datepicker" value="${endDate}">`;
            this.oldEndTime = endTime;
        } else {
            const startDate = this.startTarget.querySelector('#startTime').value;
            this.startTarget.innerHTML = `<input type="text" class="form-control" id="startTime" name="start" data-controller="datepicker" value="${startDate} ${this.oldStartTime}">`;
            const endDate = this.endTarget.querySelector('#endTime').value;
            this.endTarget.innerHTML = `<input type="text" class="form-control" id="endTime" name="end" data-controller="datepicker" value="${endDate} ${this.oldEndTime}">`;
        }
    }
}

export default CreateEventController;
