import { Controller } from 'stimulus';

export default class extends Controller {
    loadConfig() {
        const defaults = {
            mask: true,
            step: 30,
            closeOnDateSelect: true,
            validateOnBlur: false
        };
        if (this.data.has('from-today')) {
            defaults.startDate = new Date();
        }
        if (this.data.has('time-only')) {
            return { ...defaults, datepicker: false, format: 'H:i' };
        }
        if (this.data.has('date-only')) {
            return { ...defaults, timepicker: false, format: 'Ymd' };
        }
        return { ...defaults, format: 'Ymd H:i' };
    }

    connect() {
        $(this.element).datetimepicker(this.loadConfig());
    }
}
