/**
 * First we will load all of this project's JavaScript dependencies which
 * includes Vue and other libraries. It is a great starting point when
 * building robust, powerful web applications using Vue and Laravel.
 */

require('./bootstrap');

/**
 * Fire datatable init
 */

$(document).ready(() => {
    $('.datatable:not([data-controller~=datatable])').dataTable({
        searching: false,
        lengthChange: false,
        pageLength: 50,
        language: {
            sEmptyTable: '',
            sInfo: ' _TOTAL_  _START_  _END_ ',
            sInfoEmpty: ' 0  0  0 ',
            sInfoFiltered: ' _MAX_ ',
            sInfoPostFix: '',
            sInfoThousands: ',',
            sLengthMenu: '_MENU_ ',
            sLoadingRecords: '...',
            sProcessing: '...',
            sSearch: ':',
            sZeroRecords: '',
            oPaginate: {
                sFirst: '',
                sLast: '',
                sNext: '',
                sPrevious: ''
            },
            oAria: {
                sSortAscending: ': ',
                sSortDescending: ': '
            }
        }
    });
});
