import { Controller } from 'stimulus';
import { Calendar, formatDate, formatRange } from '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import listPlugin from '@fullcalendar/list';
import timeGridPlugin from '@fullcalendar/timegrid';

const DATE_FORMAT = {
    locale: 'ja',
    month: 'long',
    year: 'numeric',
    day: 'numeric',
    separator: '  '
};
const NOW_BUTTON_LOCALIZE = type => {
    const locale = { timeGridDay: '', timeGridWeek: '', dayGridMonth: '', default: '' };
    if (!{}.hasOwnProperty.call(locale, type)) {
        return locale.default;
    }
    return locale[type];
};
class CalendarController extends Controller {
    static targets = ['createModal'];

    get events() {
        if (!this.data.has('events')) {
            return [];
        }
        return JSON.parse(this.data.get('events'));
    }

    triggerCreateEventModal(info) {
        const details = {
            fromView: info.view.type,
            start: info.start,
            end: info.end,
            allDay: info.allDay
        };
        this.createModalTarget.dispatchEvent(new CustomEvent('open', { detail: details }));
    }

    connect() {
        const calendar = new Calendar(this.element, {
            plugins: [dayGridPlugin, interactionPlugin, listPlugin, timeGridPlugin],
            locale: 'ja',
            header: {
                left: 'timeGridDay,timeGridWeek,dayGridMonth',
                center: 'title',
                right: 'prev,today,next'
            },
            navLinks: true,
            selectable: true,
            views: {
                timeGridDay: {
                    buttonText: '',
                    allDayText: '',
                    slotLabelFormat: {
                        hour: 'numeric',
                        minute: 'numeric',
                        omitZeroMinute: false
                    },
                    nowIndicator: true
                },
                timeGridWeek: {
                    titleFormat: date => {
                        return `${formatDate(new Date(date.start.marker), DATE_FORMAT)}  ${formatDate(new Date(date.end.marker), DATE_FORMAT)}`;
                    },
                    titleRangeSeparator: '  ',
                    buttonText: '',
                    allDayText: '',
                    slotLabelFormat: {
                        hour: 'numeric',
                        minute: 'numeric',
                        omitZeroMinute: false
                    },
                    nowIndicator: true
                },
                dayGridMonth: {
                    buttonText: ''
                }
            },
            viewSkeletonRender: info => {
                const today = NOW_BUTTON_LOCALIZE(info.view.type);
                this.element.querySelectorAll('.fc-today-button.fc-button.fc-button-primary').forEach(element => {
                    element.innerHTML = today; // eslint-disable-line no-param-reassign
                });
            },
            select: info => {
                this.triggerCreateEventModal(info);
            },
            events: this.events,
            eventRender: info => {
                const eventElement = info.el;
                eventElement.dataset.toggle = 'modal';
                eventElement.dataset.target = '#viewCalendarEvent';
                eventElement.dataset.event = JSON.stringify({
                    title: info.event.title,
                    time: info.event.end === null ? formatDate(info.event.start, DATE_FORMAT) : formatRange(info.event.start, info.event.end, DATE_FORMAT),
                    detail: info.event.extendedProps.detail,
                    location: info.event.extendedProps.location,
                    id: info.event.id
                });
            }
        });

        calendar.render();
    }
}

export default CalendarController;
