import { Controller } from 'stimulus';

class ToggleController extends Controller {
    toggle(event) {
        try {
            if (!event.target.dataset.toggleClass) {
                throw new Error('Trigger Element does not have toggle class setting');
            }
            if (event.target.dataset.toggleId) {
                if (this.element.id === event.target.dataset.toggleId) {
                    this.element.classList.toggle(event.target.dataset.toggleClass);
                } else {
                    document.getElementById(event.target.dataset.toggleId).classList.toggle(event.target.dataset.toggleClass);
                }
            } else {
                this.element.classList.toggle(event.target.dataset.toggleClass);
            }
        } catch (e) {
            console.error(e);
        }
    }
}

export default ToggleController;
